@import url(https://fonts.googleapis.com/css?family=Roboto:400,700|Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 900px;
  margin: 0 auto;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;
}

.App-link {
  color: #61dafb;
}

#site-header {
  text-align: center;
}

.topLevel {
  margin-bottom: 10px;
}

#error {
  background-color : rgb(255, 221, 221);
  padding : 10px;
  border : 1px solid rgb(255, 150, 150);
  border-radius : 5px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#container {
    width : 80%;
    margin: auto;
}

#panel1 {
    float : left;
}

#panel2 {
    float : right;
}

.panel {
    width : 49%;
}

.mutation {
    background-color : #89ff9d;
}

#clear {
    clear : both;
}

.MuiSvgIcon-root-168 {
    cursor: pointer;
}

.mutantName {
    color: blue;
    cursor: pointer;
}

.mutantName:hover {
    text-decoration: underline;
}

#save-file {
    text-align: center;
}

#summary {
    text-align: center;
    margin:15px;
}

.back-button {
    cursor: pointer;
}
#killed {
    background-color: rgb(136, 255, 136);
}

#equivalent {
    background-color: rgb(255, 253, 133);
}

#live {
    background-color: rgb(247, 82, 82);
}

.sumPanel {
    display: inline-block;
    margin: 0 auto;

}

#sumPanel1 {
    width: 300px;
}

#clear {
    clear: both;
}

#summaryHeader {
    margin-bottom: 5px;
}

#tooltip {
    border: 1px solid rgba(100, 100, 100);
    border-radius: 5px;
    opacity: 0.8;
    display: none;
    position: absolute;
    margin-top: 10px;
    margin-left: 10px;
    padding: 5px;
    font-size: 13px;
}
#code {
    background-color: rgb(248, 248, 255);
    padding: 5px;
    margin-top: 10px;
}
#file-upload-form {
    text-align: center;
}
